import { ButtonTypes } from "@/data/appConstants";

interface ButtonProps {
  label: string;
  onClick?: () => void;
  className?: string;
  // type?: "primary" | "transparent";
  type?: ButtonTypes.PRIMARY | ButtonTypes.TRANSPARENT;
}
const typeClasses = {
  primary: "bg-[#2463eb] text-white ",
  transparent: "bg-transparent text-black hover:bg-[#2463eb] hover:text-white transition-colors duration-300 ease-in-out",
};
export default function Button({
  type = "primary",
  label,
  onClick,
  className = "",
}: ButtonProps) {
  return (
    <button onClick={onClick} className={`${typeClasses[type]} ${className}`}>
      {label}
    </button>
  );
}
